<template>
  <div class="workbench">
    <div class="top">
      <div class="security_warning">
        <div class="title">
          {{$t("workbench.boxTitle.security")}}
          <i class="blue"></i>
        </div>
        <div class="war">
          <div class="war-box" @click="turntoplatformwarning">
            <div class="war-box-title">
              <img :src="this.allStaticImg[0].armicon1" alt>
              <span style="margin-left: 0.1rem">丨{{$t("workbench.securityTxt.platform")}}</span>
            </div>
            <div class="war-box-num">{{platformwarningNum}}</div>
          </div>
          <div class="war-box" @click="devicewarning">
            <div class="war-box-title">
              <img :src="this.allStaticImg[0].armicon2" alt>
              <span style="margin-left: 0.1rem">丨{{$t("workbench.securityTxt.device")}}</span>
            </div>
            <div class="war-box-num">{{devicewarningNum}}</div>
          </div>
        </div>
      </div>
      <div>
        <div class="title">
          {{$t("workbench.boxTitle.management")}}
          <i class="blue"></i>
        </div>
        <div class="helper">
          <div class="helper-box">
            <div class="helper-box-title">
              <img :src="this.allStaticImg[0].detection" alt>
              <span style="margin-left: 0.1rem">丨{{$t("workbench.helperTxt.platform")}}</span>
            </div>
          </div>
          <div class="helper-box">
            <div class="helper-box-title">
              <img :src="this.allStaticImg[0].detection" alt>
              <span style="margin-left: 0.1rem">丨{{$t("workbench.helperTxt.Terminal")}}</span>
            </div>
          </div>
          <div class="helper-box">
            <div class="helper-box-title">
              <img :src="this.allStaticImg[0].debugging" alt>
              <span style="margin-left: 0.1rem">丨{{$t("workbench.helperTxt.Debugging")}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="operation-log">
        <div class="title">
          <i class="blue"></i>
          {{$t("workbench.boxTitle.Operation")}}
          <router-link to="/dataManage/operation" class="bto">
            <i class="el-icon-more"></i>
          </router-link>
        </div>
        <div class="log">
         <div>
           <p v-for="(item,index) in logText"
                :key="index"
                :title="item.adminUsername+item.remark">
                {{item.adminUsername}}  {{item.remark.slice(0,6)}}...
          </p>
         </div>
         <div>
           <p v-for="(item,index) in logText"
                :key="index">
                {{item.timestamp}}
          </p>
         </div>
        </div>
      </div>
    </div>

    <div class="bottom">
      <h3>{{$t("workbench.appBox.title")}}</h3>
      <!-- <p>应该管理</p> -->
      <div class="applist">
        <!-- <div class="app-box" @click="toAccessSystem">门禁系统</div>
            <div class="app-box">考勤系统</div>
        <div class="app-box" @click="toMonitoringSystem">智能监控系统</div>-->
        <div class="app-box" v-for="(item,index) in applist" :key="index">
          <div class="openDeleteApp" @click="openDeleteApp(index)">
            <img :src="allStaticImg[0].delete" alt>
          </div>
          <div class="app-box-center" @click="turnPath(index)">
            <div class="app-box-img" style="margin-right:0.2rem">
              <img :src="item.src" alt>
            </div>
            <div class="app-box-text">{{item.name}}</div>
          </div>
        </div>
        <div class="app-box" @click="addApp">
          <div class="app-box-img add-box">+</div>
          <div class="app-box-text">
            <router-link to="/monitoringLogin">{{$t("workbench.appBox.appInsert")}}</router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import APICONFIG from "../../../../../config/api.js";
import staticImg from "../../../../../../static/json/imgJson.json";

export default {
  name: "workbench",
  data() {
    return {
      logAPI: "",
      netAPI: "",
      logText: [],
      platformwarningNum: 0,
      devicewarningNum: 0,
      // 引入的所有图片
      allStaticImg: staticImg.workbenchImages,
      // applist: [
      //   {
      //     name: "门禁系统",
      //     src: staticImg.workbenchImages[1].accessControl
      //   },
      //   {
      //     name: "考勤系统",
      //     src: staticImg.workbenchImages[1].attendance
      //   },
      //   {
      //     name: "智能监控系统",
      //     src: staticImg.workbenchImages[1].monitor
      //   }
      // ]
    };
  },
  computed: {
    applist() {
      return [
        {
          name: this.$t('workbench.appList.access'),
          src: staticImg.workbenchImages[1].accessControl
        },
        {
          name: this.$t('workbench.appList.attendance'),
          src: staticImg.workbenchImages[1].attendance
        },
        {
          name: this.$t('workbench.appList.monitor'),
          src: staticImg.workbenchImages[1].monitor
        }
      ];
    }
  },
  created() {
    this.logAPI = APICONFIG().get("operation_log");
    this.netAPI = APICONFIG().get("alarm_log");
    this.findLog();
    this.findWarningNum();
  },
  methods: {
    turnPath(index) {
      // console.log(name)
      // switch (name) {
      //   case "门禁系统":
      //     this.toAccessSystem();
      //     break;
      //   case "考勤系统":
      //     break;
      //   case "智能监控系统":
      //     this.toMonitoringSystem();
      //     break;
      //   default:
      //     break;
      // }
      switch (index) {
        case 0:
          this.toAccessSystem();
          break;
        case 1:
          break;
        case 2:
          this.toMonitoringSystem();
          break;
        default:
          break;
      }
    },
    addApp() {},
    // 查询平台预警和设备预警数量
    findWarningNum() {
      this.$http
        .get(this.netAPI.findByCondition, { params: { eventSource: 1 } })
        .then(res => {
          if (res.body.status == 200) {
            // console.log(res.body.total)
            this.platformwarningNum = res.body.total;
          } else {
            this.$message.warning(res.body.message);
          }
        });
      this.$http
        .get(this.netAPI.findByCondition, { params: { eventSource: 0 } })
        .then(res => {
          if (res.body.status == 200) {
            // console.log(res.body.total)
            this.devicewarningNum = res.body.total;
          } else {
            this.$message.warning(res.body.message);
          }
        });
    },
    // 查询操作日志
    findLog() {
      this.$http.get(this.logAPI.find).then(
        res => {
          if (res.body.status == 200) {
            // console.log(JSON.stringify(res.body.data))
            this.logText.push(res.body.data[0]);
            this.logText.push(res.body.data[1]);
            this.logText.push(res.body.data[2]);
            // 格式化时间
            this.logText.forEach(item => {
              item.timestamp = this.parsedate(item.timestamp);
            });
          } else {
            this.$message.warning(res.body.message);
          }
        },
        err => {
          this.$message.warning(this.$t('common.serviceError') + err);
        }
      );
    },
    openDeleteApp(index) {
      // console.log(index)
      //弹出对话框
      this.$confirm(
        "应用删除后，该应用下所有支持的功能将全部关闭，您确定执行该操作吗?",
        this.$t('delete.tips'),
        {
          distinguishCancelAndClose: true,
          type: "warning",
          confirmButtonText: this.$t('delete.ok'),
          cancelButtonText: this.$t('delete.cancel'),
        }
      )
        .then(() => {
          //确认删除
          this.applist.splice(index, 1);
          // this.staticListUrl.splice(index, 1)
          this.$message.success(this.$t('common.deleteSuccess'));
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: this.$t('delete.canceledDeletion')
          });
        });
    },
    parsedate(date) {
      // console.log(date)
      let d = date.slice(0, 10);
      let h = date.slice(11, 19);
      return d + " " + h;
    },
    //门禁系统
    toAccessSystem() {
      this.$router.push({ path: "/accessSystem/access_manage/index" });
    },
    toMonitoringSystem() {
      // console.log(1515)
      this.$router.push({
        path: "/monitoringSystem/monitoring_system/deviceManage?type=0"
      });
    },
    // 跳转平台预警
    turntoplatformwarning() {
      // console.log(this)
      this.$router.push({ name: "预警查看", params: { num: 1 } });
    },
    // 跳转设备预警
    devicewarning() {
      this.$router.push({ name: "预警查看", params: { num: 0 } });
    }
  }
};
</script>
<style lang="scss" scoped>
.workbench {
  width: 100%;
  // min-width: 1428px;
  height: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  .top {
    width: 100%;
    height: 230px;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    margin-bottom: 0.4rem;
    > div {
      height: 100%;
      background: rgba(255, 255, 255, 1);
      border: 1px solid rgba(233, 233, 233, 1);
      box-shadow: #d1d1d1 0 1px 5px;
      border-radius: 5px;
      box-sizing: border-box;
      &:nth-child(1) {
        width: 28%;
        @media screen and(max-width:1560px){
          width: 34%;
        }
      }
      &:nth-child(2) {
        width: 39%;
        @media screen and(max-width:1560px){
          width: 27%;
        }
        // @media screen and(max-width:1366px){
        //   width: 28%;
        // }
      }
      &:nth-child(3) {
        width: 29%;
        @media screen and(max-width:1560px){
          width: 36%;
        }
      }
      > .title {
        height: 25%;
        width: 100%;
        font-size: 22px;
        display: flex;
        align-items: center;
        box-sizing: border-box;
        padding-left: 0.5rem;
        font-weight: 600;
        position: relative;
        @media screen and (max-width:1280px){
          font-size: 19px;
          padding-left: 0.4rem;
        }
      }
      .blue {
        width: 12px;
        height: 20px;
        background: rgba(67, 131, 220, 1);
        border-radius: 0px 3px 3px 0px;
        position: absolute;
        left: 0;
        top: 17px;
      }
      .el-icon-more {
        color: rgba(67, 131, 220, 1);
        position: absolute;
        top: 0;
        right: 0;
        width: 1.125rem;
        height: 1.125rem;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 0.45rem;
      }
      .war {
        width: 100%;
        height: 75%;
        display: flex;
        justify-content: space-around;
        @media screen and(max-width:1280px){
          box-sizing: border-box;
          padding: 0 0.1rem !important;
        }
        .war-box {
          width: 3.4rem;
          height: 1.8rem;
          display: flex;
          flex-direction: column;
          margin-top: 0.4rem;
          cursor: pointer;
          .war-box-title {
            width: 100%;
            height: 30%;
            display: flex;
            justify-content: center;
            align-items: center;
          }
          .war-box-num {
            width: 100%;
            height: 70%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 34px;
            color: rgba(67, 131, 219, 1);
          }
        }
      }
      .helper {
        width: 100%;
        height: 75%;
        display: flex;
        justify-content: space-between;
        box-sizing: border-box;
        padding: 0 1rem;
        @media screen and(max-width:1560px){
          flex-direction: column;
          padding: 0 10px 20px !important;
        }
        .helper-box {
          height: 1.8rem;
          margin-top: 0.4rem;
          @media screen and(max-width:1560px){
            width: 4rem;
            height: 1rem;
            margin-top: 0;
            display: flex;
            align-items: center;
          }
          .helper-box-title {
            width: 100%;
            height: 30%;
            display: flex;
            justify-content: center;
            align-items: center;
          }
        }
      }
      .log {
        width: 100%;
        height: 75%;
        box-sizing: border-box;
        padding:0 0.5rem;
        font-size: 16px;
        display: flex;
        @media screen and (max-width:1110px){
          padding: 0 0.3rem;
        }
        >div:nth-child(1){
          flex: 0.8;
         
        }
        >div:nth-child(2){
          flex: 1;
          text-align:right;
        }
        .bto {
          position: absolute;
          left: 20px;
          bottom: 10px;
          color: rgb(22, 155, 213);
        }
      }
    }
    > div:nth-child(3) {
      margin-right: 0;
    }
  }
  .bottom {
    width: 100%;
    flex: 1;
    box-sizing: border-box;
    background: rgba(255, 255, 255, 1);
    border: 1px solid rgba(233, 233, 233, 1);
    box-shadow: #d1d1d1 0 1px 5px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    > h3 {
      box-sizing: border-box;
      padding-left: 20px;
      font-size: 22px;
      display: flex;
      align-items: center;
      height: 70px;
      margin: 0;
    }
    .applist {
      flex: 1;
      width: 100%;
      box-sizing: border-box;
      padding-left: 20px;
      .app-box {
        width: 11.5%;
        padding: 25px;
        border: 1px solid rgba(237, 239, 242, 1);
        display: flex;
        justify-content: center;
        align-items: center;
        float: left;
        margin-right: 20px;
        border-radius: 5px;
        position: relative;
        &:hover {
          box-shadow: 0px 0px 18px 0px rgba(230, 233, 237, 1);
        }
        .app-box-center {
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
        }
        .openDeleteApp {
          position: absolute;
          right: 0;
          top: 0;
          cursor: pointer;
          opacity: 0;
          &:hover {
            opacity: 1;
            transition: opacity 0.5s;
          }
        }
        .add-box {
          width: 0.8rem;
          height: 0.8rem;
          border: 1px dashed #4383dc;
          margin-right: 0.2rem;
          border-radius: 0.1rem;
          display: flex;
          justify-content: center;
          align-items: center;
          color: #4383dc;
        }
      }
    }
  }
}
@media screen and (max-width: 1825px) {
    .helper{
      padding: 10px !important;
    }
}
</style>




